import React from 'react';
import Contact from '../Contact';
import ContactsDisplay from './ContactsDisplay';
import { connect } from 'react-redux';
import { savePhotoSuccess } from '../../../../Redux/profile-reducer';
import s from './ContactsDisplayContainer.module.css';
import {getUserProfile} from '../../../../Redux/profile-reducer';

class ContactsDisplayContainer extends React.PureComponent {

componentDidMount (){


     let  userId =this.props.authorizedUserId;
     
    
    this.props.getUserProfile(userId);
  
  
}


render(){

return   <ContactsDisplay profile={this.props.profile} 
                                   isAuth={this.props.isAuth} />
                                    

}


}

let mapStateToProps = (state) => ({

profile: state.profilePage.profile,
isAuth: state.auth.isAuth,
authorizedUserId: state.auth.userId

})


export default connect (mapStateToProps, {getUserProfile}) (ContactsDisplayContainer); 