import React from 'react';
import s from './Hobby.module.css';



const Hobby= (props) => {

return <>
 <div>

                  <b className={s.title}> Hobby  </b>
<p className={s.article}>
    A hobby is a regular activity done for enjoyment, typically during one's leisure time, 
    not professionally and not for pay. Hobbies include collecting themed items and objects, 
    engaging in creative and artistic pursuits, playing sports, or pursuing other amusements.
     Participation in hobbies encourages acquiring substantial skills and knowledge in that area.
      A list of hobbies changes with renewed interests and developing fashions, making it diverse and lengthy.
       Hobbies tend to follow trends in society, for example stamp collecting was popular during the nineteenth 
       and twentieth centuries as postal systems were the main means of communication, while video games are more 
       popular nowadays following technological advances. The advancing production and technology of the nineteenth 
       century provided workers with more availability in leisure time to engage in hobbies. Because of this, 
       the efforts of people investing in hobbies has increased with time.

Hobbyists may be identified under three sub-categories: casual leisure which is intrinsically rewarding,
 short-lived, pleasurable activity requiring little or no preparation, serious leisure which is the systematic pursuit of an amateur,
  hobbyist, or volunteer that is substantial, rewarding and results in a sense of accomplishment, 
  and finally project-based leisure which is a short-term often a one-off project that is rewarding.
  </p>


    </div>

<a className={s.link} href="https://en.wikipedia.org/wiki/Hobby" > https://en.wikipedia.org/wiki/Hobby</a>
</>
}

export default Hobby;
