import React from 'react';
import {useState} from 'react';

import s from '../Photos/PhotosContainer.module.css';

import PhotosForm from './PhotosForm';





const Photos = (props) => {

const [editMode,setEditMode] = useState (false);

  

    
  return <>

  {!editMode
  ?  <PhotosPage goToEditMode = {()=>{setEditMode(true)}} 
   photo1={props.photo1} 
   photo2={props.photo2} 
   photo3={props.photo3} 
     />
  
  : <PhotosForm savePhotoSuccess1={props.savePhotoSuccess1}
savePhotoSuccess2={props.savePhotoSuccess2}
savePhotoSuccess3={props.savePhotoSuccess3}
  deactivateEditMode={()=>{setEditMode(false)}} />
  }
       </>

  


   
}


 export default Photos;



const PhotosPage = (props) => {


  return <div>
  
  <div> 
   <h2> My Photos </h2>
  </div>
  
  
  {<div> <button onClick = {props.goToEditMode}> Edit </button> </div>
  }
  
   
  
  <div >
  
   <b> 1. </b>
  <img src={props.photo1} className={s.photos}/>
  
  
  </div>
  
  
  <div  >
    <b> 2. </b>  
   
    <img src= {props.photo2} className={s.photos} />
   
        
  </div>
    
  
  <div> 
   <b>3. </b>  
  
  <img src={props.photo3} className={s.photos} />
   
  </div>
  
   
  </div>
  
  
  }
  


 


