import React, { useState, useEffect, useCallback } from 'react';
import {connect} from 'react-redux';
import SearchUserReduxForm from './SearchUser';
import DisplayUser from './DisplayUser';

import { follow, unfollow,
     toggleFollowingProgress,
    requestUsersbyName, requestUsers} from  '../../../Redux/users-reducer';
import Preloader from '../../common/Preloader/Preloader';
import { compose } from 'redux';
import { withAuthRedirect } from '../../Dialogs/hoc/withAuthRedirect';
import getState from 'react';



const SearchUser =React.memo((props) => {


  let  [editMode, setEditeMode]= useState(true);

 
 

  const onSubmit = (formData) => {
  

   const name = formData.searchedName;
 
   props.requestUsersbyName(name).then(
    () => {

    setEditeMode(false);

    }
   );

   }


 




  
   return <>

      {props.isFetching ?  <Preloader /> : null}
   <div>  
   
    {editMode && <SearchUserReduxForm  onSubmit={onSubmit}   /> }
       </div>

    <div>
    {!editMode && <DisplayUser users={props.searchedUsers}   followingInProgress= {props.followingInProgress}
          unfollow = {props.unfollow}
          follow= {props.follow} 
          {...props}/> }

   </div>
</>

  });
  
  
  const mapStateToProps = (state) => ( {


  
  searchedUsers:  state.usersPage.searchedUsers,
  //userNamebySearch: state.usersPage.userNamebySearch,
  isFetching: state.usersPage.isFetching,
  followingInProgress: state.usersPage.followingInProgress
  

  })
  
  export default compose( connect(mapStateToProps, {requestUsersbyName, toggleFollowingProgress, follow, 
    unfollow}), withAuthRedirect ) (SearchUser);