import React, {useState} from 'react';
import Iframe from 'react-iframe';
import Preloader from '../../common/Preloader/Preloader';
import ProfileMusicForm from './ProfileMusic';

import { withAuthRedirect } from '../../Dialogs/hoc/withAuthRedirect';
import { compose } from 'redux';
import {connect} from 'react-redux';
import s from '../ProfileMusic/ProfileMusicContainer.module.css';
import userPhoto from '../../../.vs/src/assect/images/user.png';


const ProfileMusic = React.memo((props) => {


let [editMode, setEditeMode] = useState(true);




  if(!props.isAuth) {
  return <Preloader />
  }
  
  
 

    const activateEditeMode = () => {

      setEditeMode(true);

    }

 const deactivateEditeMode = () => {

      setEditeMode(false);

    }

 

   return    (


       <div> 
         
  {editMode  && <ProfileMusicPage goToEditMode = {deactivateEditeMode}  
  isAuth={props.isAuth } deactivateEditeMode={props.deactivateEditeMode}/>}

{/*!editMode &&

     <div>
<ProfileMusicForm  saveMusicSuccess1={props.saveMusicSuccess1}
 saveMusicSuccess2={props.saveMusicSuccess2} 
 saveMusicSuccess3={props.saveMusicSuccess3} activateEditeMode={activateEditeMode} />

 </div>

*/}

       
       

      </div>

    

   )
   
});







const ProfileMusicPage= (props) => {






return <div>

<div> 
 <h2> Here is my favorite music!!!</h2>
</div>


{props.isAuth && <div> <button onClick = {props.goToEditMode}> Edit </button> </div>
}








<div>

 <b> 1. </b>
<iframe width="560" height="315" src="https://www.youtube.com/embed/e1uqfO2-oJA"
 frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>


</div>


<div>
  <b> 2. </b>  
 
  <iframe width="560" height="315" src="https://www.youtube.com/embed/D67lR7Qy_wk" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
 
      
</div>



<div > 
 <b>3. </b>  


 <iframe width="560" height="315" src="https://www.youtube.com/embed/oHs98TEYecM" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>

</div>



</div>


}


  let mapStateToProps = (state) => ({

  profile: state.profilePage.profile,
  music1: state.profilePage.music1,
  music2: state.profilePage.music2,
  music3: state.profilePage.music3,
  isAuth: state.auth.isAuth

  });
 
 
 
export default compose (
    connect(mapStateToProps, {}),
    withAuthRedirect)(ProfileMusic); 






